import client from '../lib/client'
import {
    LOGIN_USER,
    REGISTER_USER,
    AUTH_USER,
    LOGOUT_USER
} from './types.js'
import * as domain from '../lib/server'

//로그인
export function loginUser(dataToSubmit){
    const req = client.post(`${domain.default}api/auth/login`, dataToSubmit)
        .then(res => res)

    return {
        type: LOGIN_USER,
        payload: req
    }
}

//회원가입
export function registerUser(dataToSubmit){
    const req = client.post(`${domain.default}api/customers/register`, dataToSubmit)
        .then(res => res)

    return {
        type: REGISTER_USER,
        payload: req
    }
}

//인증
export function auth(){
    const req = client.get(`${domain.default}api/auth/`)
                .then(response => {
                    return response;
                })


    return {
        type: AUTH_USER,
        payload: req
    }
}

//로그아웃
export function logout() {
    const req = client.get(`${domain.default}api/auth/logout`)
                    .then(response => response)

    return {
        type: LOGOUT_USER,
        payload: req
    }
}