import React, { useEffect } from "react";
import AuthTemplate from "../components/auth/AuthTemplate";
import styled, { css } from "styled-components";
import palette from "../lib/styles/palette";
import Button from "../components/common/Button";
import { Link } from "react-router-dom";
import Moment from 'react-moment'
import { useDispatch, useSelector } from 'react-redux'
import * as storeAction from '../_actions/store_actions'
import * as userAction from '../_actions/user_actions'
import * as resAction from '../_actions/reserve_actions'
import Iamport from 'react-iamport'

const MenuBlock = styled.div`
  padding: 0.5rem 0.75rem;
  border-radius: 0.25rem;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid ${palette.gray[5]};
  width: 100%;
  margin-right: 1rem;
  ${(props) =>
    props.bold &&
    css`
      font-weight: bold;
    `}
  ${(props) =>
    props.right &&
    css`
      text-align: right;
    `}
`;



const ReservePage = (props) => {
  const { storeid, time, date } = props.match.params;
  const dispatch = useDispatch();

  const { findStoreInfo } = useSelector((state) => state.store);
  const { account } = useSelector(state => state.user);

  useEffect(() => {
    dispatch(userAction.auth())
    dispatch(storeAction.storeInfo(storeid))
  }, [dispatch, storeid])

  return (
    <AuthTemplate>
      <h3>예약확인</h3>
      <MenuBlock right>
        <table width="100%">
          <tr>
            <td><p>예약 날짜</p></td>
            <td><p>{new Date(Date.now() + (date * 86400 * 1000)).format('MM월 dd일')}</p></td>
          </tr>
          <tr>
            <td><p>예약 시간</p></td>
            <td><p>{time ? `${parseInt(time)}:00 ~ ${parseInt(time) + 2}:00` : '잘못된 시간입니다.'}</p></td>
          </tr>
          <tr>
            <td>예약자</td>
            <td text-align="right">
              {account &&
                account.account &&
                <p>{account.account.nickname}</p>}</td>
          </tr>
          <tr>
            <td>선수금</td>
            <td>{findStoreInfo &&
              findStoreInfo.store &&
              <p>{findStoreInfo.store.prepay} 원</p>}</td>
          </tr>
        </table>
      </MenuBlock>
      {findStoreInfo &&
        findStoreInfo.store &&
        <Iamport
        identificationCode="imp94333879"
        params={{
          pg: "html5_inicis",
          pay_method: "card",
          merchant_uid: `MILLP${findStoreInfo.store.storeid}-${new Date().getTime()}`,
          name: `${findStoreInfo.store.name}`,
          amount: findStoreInfo.store.prepay,
          buyer_email: `${account.account.email}`,
          buyer_name: `${account.account.nickname}`,
          //m_redirect_url: `http://millplanning.ml/api/customers/reserve/payment/${findStoreInfo.store.storeid}`
        }}
        onFailed={err => console.log(err)}
        onSuccess={res => {
          const data = {
            imp_uid: res.imp_uid,
            merchant_uid: res.merchant_uid,
            name: res.name,
            amount: res.amount,
            buyer_name: res.buyer_name,
            buyer_tel: res.buyer_tel,
            storeid,
            reservedate: new Date(Date.now() + (date * 86400 * 1000)).format('yyyyMMdd'),
            prepay: findStoreInfo.store.prepay,
            peoples: 1,
            reservetime: parseInt(time) * 10000
          }
          dispatch(resAction.doReserve(data))
        }}
        render={(renderProps) => (
          <Button cyan fullWidth onClick={renderProps.onClick}>예약하기</Button>
        )}
      />
}
      
    </AuthTemplate>
  );
};

export default ReservePage;
